import React from 'react';

class Footer extends React.Component {
    state = {  }
    render() { 
        return (
        <div id="footer">
            <section className="footer-contact-button">
                <p className="heading">
                    Contact me for work
                </p>
                <p className="text">
                    Send me your email
                </p>
                <div className="input-area">
                    <form>
                        <input type="email" name="email" placeholder="Your Email" className="footer-input"/>
                        <div className="text-box">
                            <a href="#" type="button" id="button" className="btn btn-white btn-animate">Send it</a>
                        </div>                       
                    </form>
                </div>
            </section>
        </div> 
        );
    }
}
 
export default Footer;